<template>
  <div id="new-member">
    <h3>New Member</h3>
    <div class="row">
      <form @submit.prevent="saveMember" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="member_id" required>
            <label>GSA Member ID#</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label>Name</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="major" required>
            <label>major</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="email" required>
            <label>email</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="phone" required>
            <label>Phone</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" required>
            <label>position (NA if 'none')</label>
          </div>
        </div>

        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default{
  name: 'new-member',
  data(){
    return {
      gsa_id: null,
      name: null,
      major: null,
      email: null,
      phone: null,
      position: null
    }
  },
  methods: {
    saveMember(){
      db.collection('GSA').add({
        gsa_id: this.member_id,
        name: this.name,
        major: this.major,
        email: this.email,
        phone: this.phone,
        position: this.position
      })
      .then(docRef =>{
        this.$router.push('/')
      })
      .catch(error => console.log(err))
    }
  }
}
</script>



