<template>
  <div id="edit-member">
    <h3>Edit Member</h3>

    <div class="row">
      <form @submit.prevent="updateMember" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="member_id" required>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" placeholder="name" required>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="major" placeholder="major"  required>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="email" placeholder="email"  required>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="phone" placeholder="phone"  required>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" placeholder="position (NA if 'none')"  required>
          </div>
        </div>

        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>


<script>
import db from './firebaseInit'
export default {
  name: 'edit-member',
  data(){
    return {
    gsa_id: null,
    name: null,
    email: null,
    major: null,
    phone: null,
    position: null
    }
  },
  beforeRouteEnter (to, from, next){
    db.collection('GSA').where('gsa_id', '==', to.params.member_id).get()
    .then(querySnapshot =>{
      querySnapshot.forEach(doc =>{
        next(vm =>{
          vm.member_id = doc.data().gsa_id
          vm.name = doc.data().name
          vm.email = doc.data().email
          vm.major = doc.data().major
          vm.phone = doc.data().phone
          vm.position = doc.data().position

        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData (){
      db.collection('GSA').where('gsa_id', '==', this.$route.params.member_id)
      .get().then(querySnapshot =>{
        querySnapshot.forEach(doc =>{
          this.member_id = doc.data().gsa_id
          this.name = doc.data().name
          this.email = doc.data().email
          this.major = doc.data().major
          this.phone = doc.data().phone
          this.position = doc.data().position
        })
      })
    },
    updateMember(){
    db.collection('GSA').where('gsa_id', '==', this.$route.params.member_id)
    .get().then(querySnapshot =>{
      querySnapshot.forEach(doc =>{
        doc.ref.update({
        gsa_id: this.member_id,
        name: this.name,
        major: this.major,
        email: this.email,
        phone: this.phone,
        position: this.position
        })
        .then(()=>{
          this.$router.push({name: 'view-Member', params: {gsa_id: this.member_id}})
        })
      })
    })
   }
 }
}
</script>



