<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Members</h4></li>
      <li v-for="member in members" v-bind:key="member.id" class="collection-item">
      <div class="chip">{{member.major}}</div>
      {{member.member_id}}: {{member.name}}

      <router-link class="secondary-content"
      v-bind:to= "{name:'view-Member', params: {member_id: member.member_id}}">
      <i class="fa fa-eye"></i>
      </router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to='/new' class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default{
  name: 'dashboard',
  data(){
  return {
      members: []
    }
  },
  created (){
    db.collection('GSA').orderBy('major').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {

        const data = {
          'id': doc.id,
          'member_id': doc.data().gsa_id,
          'name': doc.data().name,
          'email': doc.data().email,
          'phone': doc.data().phone,
          'major': doc.data().major,
          'position': doc.data().position
        }
        this.members.push(data)
      })
    })
  }
}
</script>



