<template>
  <div id="view-member">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{name}}</h4></li>

      <li class="collection-item"><b>GSA Member ID#:</b> {{member_id}}</li>
      <li class="collection-item"><b>major:</b> {{major}}</li>
      <li class="collection-item"><b>email:</b> {{email}}</li>
      <li class="collection-item"><b>phone:</b> {{phone}}</li>
      <li class="collection-item"><b>position:</b> {{position}}</li>
    </ul>

    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deletehim" class="btn red">Delete</button>

    <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-member', params: {member_id: member_id}}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>


  </div>
</template>

<script>
import db from './firebaseInit'
export default{
  name: 'view-member',
  data(){
    return {
      gsa_id: null,
      name: null,
      email: null,
      major: null,
      phone: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next){
    db.collection('GSA').where('gsa_id', '==', to.params.member_id).get()
    .then(querySnapshot =>{
      querySnapshot.forEach(doc =>{
        next(vm =>{
          vm.member_id = doc.data().gsa_id
          vm.name = doc.data().name
          vm.email = doc.data().email
          vm.major = doc.data().major
          vm.phone = doc.data().phone
          vm.position = doc.data().position

        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData (){
      db.collection('GSA').where('gsa_id', '==', this.$route.params.member_id)
      .get().then(querySnapshot =>{
        querySnapshot.forEach(doc =>{
          this.member_id = doc.data().gsa_id
          this.name = doc.data().name
          this.email = doc.data().email
          this.major = doc.data().major
          this.phone = doc.data().phone
          this.position = doc.data().position
        })
      })
    },
    deletehim () {
      if(confirm('Are you sure?')) {
        db.collection('GSA').where('gsa_id', '==', this.$route.params.member_id)
        .get().then(querySnapshot =>{
          querySnapshot.forEach(doc =>{
            doc.ref.delete()
            this.$router.push('/')
          })
        })
      }
   }
 }
}

</script>



